import React from 'react'

const ChapterView = ({ manga, chapter }) => {
    const pages = chapter.page_array
    if (pages) {
        return (
            <div>
                {pages.map(page => 
                    <img src={page} alt="NAH" height="877" width="620" />
                )}
            </div>
        )
    } else {
        return (
            <div>
                Not yet
            </div>
        )
    }
}

export default ChapterView