import React, { useState } from 'react'
import MangaForm from './components/mangaform'
import ChapterView from './components/chapterview'
import mangadexAPI from './services/mgapi'

const App = () => {
    const [ manga, setManga ] = useState({})
    const [ chapter, setChapter] = useState({})
    const [ mangaID, setMangaID ] = useState('')
    const [ chapterID, setChapterID ] = useState('')

    const handleMangaIDChange = event => {
        setMangaID(event.target.value)
    }

    const handleChapterIDChange = event => {
        setChapterID(event.target.value)
    }

    const displayChapter = event => {
        event.preventDefault()
        console.log('SUBMITTED!')

        mangadexAPI
            .getManga(mangaID)
            .then(data => {
                setManga(data)
            })

        mangadexAPI
            .getChapter(chapterID)
            .then(data => {
                setChapter(data)
            })

        setMangaID('')
        setChapterID('')
    }

    return (
        <div>
            <h1>Manga Reader</h1>
            <MangaForm 
                displayChapter={displayChapter}
                newMangaID={mangaID}
                newChapterID={chapterID}
                handleMangaIDChange={handleMangaIDChange}
                handleChapterIDChange={handleChapterIDChange}
            />
            <ChapterView
                manga={manga}
                chapter={chapter}
            />
        </div>
    )
}

export default App