import React from 'react'

const MangaForm = props => {
    return (
        <form onSubmit={props.displayChapter}>
            <div>
                Manga ID: <input value={props.newMangaID} onChange={props.handleMangaIDChange} />
            </div>
            <div>
                Chapter ID: <input value={props.newChapterID} onChange={props.handleChapterIDChange} />
            </div>
            <div>
                <button type="submit">Search</button>
            </div>
        </form>
    )
}

export default MangaForm